import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { emit as aEmit } from './../../actions/analytics';

import * as playlist from './../../actions/playlist';

class FollowersPlaylist extends Component {
  constructor(props) {
    super();

    this.state = {
      isFollowing: props.playlist.isFollowing || false,
      count: props.playlist.playlist.followers.total
    };

    this._trigger = this._trigger.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.playlist.isFollowing !== nextProps.playlist.isFollowing) {
      this.setState({
        isFollowing: nextProps.playlist.isFollowing
      })
    }
  }

  componentDidMount() {
    if (this.props.playlist.isFollowing === null) {
      this.props.actions.isFollowing(this.props.playlist.playlist.id, this.props.playlist.playlist.owner.id, this.props.auth.user.id);
    }
  }

  render() {
    return (
      <div className="viewer_playlist_tools_item" data-tip="Follow this playlist on Spotify" data-place="bottom" onClick={ this._trigger }>
        <span className={ this.state.isFollowing ? 'is_following' : '' }><i className="icon_like"></i> { this.state.count } </span>
      </div>
    );
  }

  _trigger() {
    if (this.state.isFollowing) {
      this.props.actions.unfollow(this.props.playlist.playlist.owner.id, this.props.playlist.playlist.id);
      this.setState({
        isFollowing: false,
        count: this.state.count === 0 ? 0 : this.state.count - 1
      });
      aEmit('viewPlaylist', 'unfollow', this.props.playlist.playlist.id);
    } else {
      this.props.actions.follow(this.props.playlist.playlist.owner.id, this.props.playlist.playlist.id);
      this.setState({
        isFollowing: true,
        count: this.state.count + 1
      });
      aEmit('viewPlaylist', 'follow', this.props.playlist.playlist.id);
    }
  }
}

const mapStateToProps = (state) => {
  return {
    playlist: state.playlist,
    auth: state.auth
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(playlist, dispatch)
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FollowersPlaylist);



