import React, { Component } from 'react';
import { emit as aEmit } from './../../actions/analytics';

let share = (url, title, image, network, place) => {
  let socialUrls = {
    fb: `http://www.facebook.com/sharer.php?s=100&p[url]=${url}&p[images][0]=${image}&p[title]=${title}&p[summary]=bla bla`,
    tw: `https://twitter.com/intent/tweet?text=Hey! Check out this playlist ${title}, created with @magicplaylistco &url=${url}`,
    in: `https://www.linkedin.com/shareArticle?mini=true&url=${url}&title=${title}&summary=Get the playlist of your dreams based on a song&source=`,
    gl: `https://plus.google.com/share?url=${url}`,
    tb: `http://www.tumblr.com/share/link?url=${url}&name=${title}&description=Get the playlist of your dreams based on a song`
  };
  if (network === 'fb') {
    FB.ui({
      method: 'feed',
      link: url,
      app_id: '1064937730242129',
      caption: 'magicplaylist.co',
      picture: image,
      name: title,
      description: `Get the playlist of your dreams based on a song`,
      scope: 'popup'
    });
  } else {
    open(
      socialUrls[network],
      'Share',
      'height=380,width=660,resizable=0,toolbar=0,menubar=0,status=0,location=0,scrollbars=0'
    );
  }
  aEmit('share', `share${place}`, network);
};

const Share = ({ url, title, image, place }) => {
  return (
    <div className="share_container">
      <div className="share_container_item" onClick={ () => { share(url, title, image, 'fb', place) } }><i className="icon_facebook"></i></div>
      <div className="share_container_item" onClick={ () => { share(url, title, image, 'tw', place) } }><i className="icon_twitter"></i></div>
      <div className="share_container_item" onClick={ () => { share(url, title, image, 'tb', place) } }><i className="icon_tumblr"></i></div>
      <div className="share_container_item" onClick={ () => { share(url, title, image, 'gl', place) } }><i className="icon_google_plus"></i></div>
    </div>
  );
};

export default Share;



