import SpotifyClient from './../core/SpotifyClient';

let Spotify = SpotifyClient.instance;

const setSuggestions = (tracks) => {
  return {
    type: 'SET_SUGGESTIONS',
    data: tracks
  };
};

const spotifySearch = (text) => {
  return (dispatch) => {
    Spotify.search(text).then(response => {
      let tracks = response.map(track => {
        return {
          track: track.name,
          artist: track.artists.first().name,
          id: track.id
        }
      });
      dispatch(setSuggestions(tracks));
    }).catch(response => {
      dispatch(response);
    });
  };
};

export const getSuggestions = (text) => {
  return (dispatch) => {
    dispatch(spotifySearch(text));
  };
};



