import React, { Component } from 'react';
import ReactTooltip from 'react-tooltip';

import Share from './../Share';
import { emit as aEmit } from './../../actions/analytics';
import FollowersCount from './../../containers/FollowersCount';

let customStyles = (index) => {
  let className = '';
  if (index === 0) {
    className = 'grid_playlist_big';
  }
  if (index === 3 || index === 4 || index === 5) {
    className = 'grid_playlist_medium';
  }
  return className;
};

let byTracks = (tracks, title) => {
  return (
    <div className="grid_wrapper grid_recommendations">
      <h3>{ title }</h3>
      {
        tracks.map((track, index) => {
          return (
            <div className="grid_playlist" key={ index }>
            <a
              className="grid_playlist_cover"
              href={`#/create/${track.id}`}
              style={{ backgroundImage: `url(${track.album.images[0].url})` }}
              onClick={ aEmit.bind(null, 'create', 'girdRecomendation', track.id) }
            >
            </a>
            <div className="grid_playlist_title">
              <p>{ track.name }</p>
              <span>{ track.artists[0].name }</span>
            </div>
          </div>
            );
        })
      }
      <ReactTooltip effect="solid"/>
    </div>
  );
};

let byPlaylist = (playlists, title, loged) => {
  return (
    <div className="grid_wrapper">
      <h3>{ title }</h3>
      {
        playlists.map((playlist, index) => {
          return (
            <div className={`grid_playlist ${customStyles(index)}`} key={ index }>
            <a
              className="grid_playlist_cover"
              href={`#/${playlist.user_id}/p/${playlist.spotify_id}`}
              style={{ backgroundImage: `url(${playlist.image})` }}
              onClick={ aEmit.bind(null, 'view', 'girdRecent', playlist.spotify_id) }
            >
            </a>
            <div className="grid_playlist_title">
              <p>{ playlist.title }</p>
              <span>Based on <span className="based_on">{ playlist.based_on }</span></span>
            </div>
            <div className={ `grid_playlist_tools ${loged}_playlist_tools` }>
              <a data-tip="Play on Spotify" data-place="top"
                href={ `https://open.spotify.com/user/${playlist.user_id}/playlist/${playlist.spotify_id}` }
                target="_blank"
                onClick={ aEmit.bind(null, 'view', 'girdPlay', playlist.spotify_id) }
              >
                <i className="icon_play"></i>
              </a>
              {
                loged
                ?
                <div data-tip="Followers" data-place="top" className="followers_slot">
                  <FollowersCount user={ playlist.user_id } playlist={ playlist.spotify_id }/>
                </div>
                :
                ''
              }
              <div className="share-slot">
                <span><i className="icon_share"></i></span>
                <Share
                  url={ `http://magicplaylist.co/${playlist.user_id}/p/${playlist.spotify_id}` }
                  image={ playlist.image }
                  title= { playlist.title }
                  place="GridPlaylist"
                />
              </div>
            </div>
          </div>
            );
        })
      }
      <ReactTooltip effect="solid"/>
    </div>
  );
};

const GridPlaylists = ({ playlists, title, hard, loged }) => {
  return hard ? byPlaylist(playlists, title, loged) : byTracks(playlists, title)
};

export default GridPlaylists;



