import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { hashHistory as history } from 'react-router';

import * as authActions from './../../actions/auth';
import { emit as aEmit } from './../../actions/analytics';

import Search from './../Search';
import User from './../../components/User';

import VIDEOS from './../../core/videos.js';
import IMAGES from './../../core/images';

class HeroContainer extends Component {
  constructor() {
    super();
    this.state = {
      value: ''
    };

    this.video_random = Math.floor(Math.random() * 5);

    this._loadExample = this._loadExample.bind(this);
    this._onSuggestionSelected = this._onSuggestionSelected.bind(this);
  }

  render() {
    return (
      <div className="hero">
        <header>
          <div>
            <a href="#" className="mp_logo"></a>
          </div>
          <div>
            <User
              login={this.props.authActions.login}
              logout={this.props.authActions.logout}
              user={this.props.auth.user}
              isLoged={this.props.auth.loged}
            />
          </div>
        </header>
        <div className="hero_content">
          <span className="title">Get the playlist of your dreams based on a song.</span>
          <Search onSuggestionSelected={ this._onSuggestionSelected } placeholder="Type the name of a song" />
          <div className="song-example">ex: <span onClick={this._loadExample}>{VIDEOS[this.video_random].title}</span></div>
        </div>
        <div className="video_cover"></div>
        <div className="video_container">
          <iframe
            id="hero_video"
            src={`https://www.youtube.com/embed/${VIDEOS[this.video_random].youtube_id}?rel=0&amp;controls=0&amp;showinfo=0&amp;autoplay=1&amp;loop=1&amp;modestbranding=1&amp;iv_load_policy=3&amp;enablejsapi=1&amp;vq=medium&amp;playlist=${VIDEOS[this.video_random].youtube_id}`}
            frameborder="0"
            allowfullscreen
            onLoad={this._muteVideo}
          >
          </iframe>
          <div className="background_mobile" style={ { backgroundImage: `url(${IMAGES[this.video_random].url})` } }></div>
        </div>
      </div>
    );
  }

  _muteVideo() {
    let player = new YT.Player('hero_video', {
      events: {
        'onReady': () => {
          player.mute();
        }
      }
    });
  }

  _onSuggestionSelected(event, { suggestion }, avoidAnalytics) {
    history.push(`/create/${suggestion.id}`);
    if (!avoidAnalytics) {
      aEmit('create', 'suggestion', suggestion.id);
    }
  }

  _loadExample() {
    this._onSuggestionSelected(
      null,
      {
        suggestion: {
          track: VIDEOS[this.video_random].track,
          artist: VIDEOS[this.video_random].artist,
          id: VIDEOS[this.video_random].spotify_id
        }
      },
      true
    );
    aEmit('create', 'example', VIDEOS[this.video_random].spotify_id);
  }

}

const mapStateToProps = (state) => {
  return {
    auth: state.auth
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    authActions: bindActionCreators(authActions, dispatch)
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HeroContainer);



