import SpotifyClient from './../core/SpotifyClient';
import { hashHistory as history } from 'react-router';

import { showModalError } from './app';
import { emit as aEmit } from './analytics';

let Spotify = SpotifyClient.instance;

const loadingPlaylist = (isLoading) => {
  return {
    type: 'IS_NEW_PLAYLIST_LOADING',
    data: isLoading
  };
};

const setPlaylist = (data) => {
  return {
    type: 'SET_NEW_PLAYLIST',
    data: data
  };
};

export const removeTrack = (index) => {
  return {
    type: 'REMOVE_TRACK',
    data: index
  };
};

const pushTrack = (track) => {
  return {
    type: 'PUSH_TRACK',
    data: track
  };
};

const confirmModalStatus = (close) => {
  return {
    type: 'CONFIRM_MODAL_STATUS',
    data: close
  };
};

const setTracksFeatures = (data) => {
  return {
    type: 'SET_TRACKS_FEATURES',
    data: data
  };
};

const setSavedPlaylist = (data) => {
  return {
    type: 'SET_SAVED_PLAYLIST',
    data: data
  };
};

export const addTrack = (id) => {
  return dispatch => {
    Spotify.getTrack(id).then((track) => {
      dispatch(pushTrack(track));
    });
    aEmit('createPlaylist', 'addTrack', id);
  };
};

const spotifyCreatePlaylist = (trackId, country) => {
  return (dispatch) => {
    Spotify.createPlaylist(trackId, country).then(response => {
      dispatch(setPlaylist(response));
    }).catch(response => {
      dispatch(showModalError('No se encontro el tema'));
      dispatch(loadingPlaylist(false));
    });
  };
};

export const createPlaylsit = (trackId, country) => {
  return (dispatch) => {
    dispatch(loadingPlaylist(true));
    dispatch(spotifyCreatePlaylist(trackId, country));
  };
};

export const isLoading = (value) => {
  return dispatch => {
    dispatch(loadingPlaylist(value));
  };
};

export const savePlaylist = (userId, name, isPublic, tracks, trackId) => {
  return dispatch => {
    dispatch(loadingPlaylist(true));
    Spotify.savePlaylist(userId, name, isPublic, tracks).then(playlist => {
      dispatch(getPlaylistFeature(tracks, playlist));
      history.push(`/${playlist.owner.id}/p/${playlist.id}`);
      aEmit('magicData', 'idPlaylist', playlist.id);
      aEmit('magicData', 'urlPlaylist', playlist._external_urls.spotify);
      aEmit('fullData', trackId, userId, playlist.id);
    });
  };
};

let getPlaylistFeature = (trackIds, playlist) => {
  return dispatch => {
    let ids = trackIds.map(track => track.id);
    Spotify.getTracksFeatureResume(ids).then(features => {
      dispatch(setTracksFeatures(features));
      dispatch(setSavedPlaylist(playlist));
      dispatch(confirmModalStatus(true));
      dispatch(loadingPlaylist(false));
    });
  };
};

export const closeConfirmModal = () => {
  return dispatch => {
    dispatch(confirmModalStatus(false));
  };
};



