import React, { Component } from 'react';
import { emit as aEmit } from './../../actions/analytics';

const Footer = ({ login, loged }) => {
  return (
    <div>
      {
        !loged
        ?
        <div className="create_account_container">
          <h3>Create your playlist for any moment, any time, now.</h3>
          <div className="signin_btn" onClick={ login }><i className="icon_spotify"></i> Connect with Spotify</div>

          <div className="mp_spotify_screenshot"></div>
        </div>
        :
        ''
      }
      <footer>
        <div className="media_featured">
          <h4>Featured on</h4>
          <ul>
            <li><a href="https://www.producthunt.com/tech/magic-playlist" className="media_producthunt" alt="Feaatured on Product Hunt" target="_blank"></a></li>
            <li><a href="http://www.cnet.com/how-to/create-spotify-playlists-based-on-one-song-with-magicplaylist/" className="media_cnet" alt="Feaatured on CNET" target="_blank"></a></li>
            <li><a href="http://www.gizmodo.com.au/2016/02/how-to-find-the-best-spotify-playlists/" className="media_gizmodo" alt="Feaatured on Gizmodo" target="_blank"></a></li>
            <li><a href="http://lifehacker.com/magicplaylist-creates-spotify-playlists-based-on-a-sing-1739415795" className="media_lifehacker" alt="Feaatured on Life Hacker" target="_blank"></a></li>
          </ul>
        </div>
        <div className="footer_links">
          <p>Created with <span className="icon_like"></span> in Córdoba, Argentina</p>
        </div>
        <div className="footer_social">
          <ul>
            <li>
              <a href="https://www.facebook.com/magicplaylist" className="social_network" target="_blank" onClick={ aEmit.bind(null, 'social', 'fb', '') }>
                <i className="icon_facebook"></i>
              </a>
            </li>
            <li>
              <a href="https://twitter.com/magicplaylistco" target="_blank" className="social_network" onClick={ aEmit.bind(null, 'social', 'tw', '') }>
                <i className="icon_twitter"></i>
              </a>
            </li>
          </ul>
        </div>
      </footer>
    </div>
  );
};

export default Footer;



