import SpotifyClient from './../core/SpotifyClient';

let Spotify = SpotifyClient.instance;

const loadingPlaylist = (isLoading) => {
  return {
    type: 'IS_PLAYLIST_LOADING',
    data: isLoading
  };
};

const setPlaylist = (playlist) => {
  return {
    type: 'SET_PLAYLIST',
    data: playlist
  };
};

const updateFollowers = (add) => {
  return {
    type: 'UPDATE_FOLLOWERS',
    data: add
  };
};

const setPlaylistFollowers = (playlistId, followers) => {
  return {
    type: 'SET_PLAYLIST_FOLLOWERS',
    data: {
      playlistId,
      followers
    }
  };
};

const setPlaylistFollowersLoading = (playlistId) => {
  return {
    type: 'SET_PLAYLIST_FOLLOWERS_LOADING',
    data: {
      playlistId
    }
  };
};


const setIsFollowing = (data) => {
  return {
    type: 'SET_PLAYLIST_IS_FOLLOWING',
    data: data
  };
};

const spotifyGetPlaylist = (userId, playlistId) => {
  return (dispatch) => {
    Spotify.getPlaylist(userId, playlistId).then(response => {
      dispatch(setPlaylist(response));
      dispatch(loadingPlaylist(false));
    }).catch(response => {
      dispatch(response);
    });
  };
};

export const getPlaylist = (userId, playlistId) => {
  return (dispatch) => {
    dispatch(loadingPlaylist(true));
    dispatch(spotifyGetPlaylist(userId, playlistId));
  };
};

export const isLoading = (value) => {
  return dispatch => {
    dispatch(loadingPlaylist(value));
  };
};

export const follow = (userId, playlistId) => {
  return dispatch => {
    Spotify.followPlaylist(userId, playlistId);
    dispatch(updateFollowers(true));
  };
};

export const unfollow = (userId, playlistId) => {
  return dispatch => {
    Spotify.unfollowPlaylist(userId, playlistId);
    dispatch(updateFollowers(false));
  };
};

export const getFollowers = (userId, playlistId) => {
  return (dispatch) => {
    Spotify.getPlaylist(userId, playlistId).then(response => {
      // dispatch(setPlaylistFollowersLoading(playlistId));
      dispatch(setPlaylistFollowers(playlistId, response.followers.total));
    });
  };
};

export const isFollowing = (ownerId, playlistId, userId) => {
  return (dispatch) => {
    Spotify.isFollowing(ownerId, playlistId, userId).then(response => {
      dispatch(setIsFollowing(response[0]));
    });
  };
};



