import { stateTree } from './../store/state';
import fusenrux from './../helpers/fusenrux';

const initialState = Object.assign({}, stateTree.app);

export default (state = initialState, action) => {
  const reducer = {};

  reducer.SET_COUNTRY = () => {
    let newState = Object.assign({}, state);
    newState.country = action.data;
    return newState;
  };

  reducer.MODAL_ERROR = () => {
    let newState = Object.assign({}, state);
    newState.modalError.show = action.data.show;
    newState.modalError.message = action.data.message;
    return newState;
  };

  reducer.SET_PLAYLIST_FOLLOWERS_LOADING = () => {
    let newState = Object.assign({}, state);
    newState.gridPlaylistList[action.data.playlistId] = newState.gridPlaylistList[action.data.playlistId] ? newState.gridPlaylistList[action.data.playlistId] : {};
    return newState;
  };

  reducer.SET_PLAYLIST_FOLLOWERS = () => {
    let newState = Object.assign({}, state);
    newState.gridPlaylistList[action.data.playlistId] = { count: action.data.followers };
    return newState;
  };

  reducer.DEFAULT = () => state;

  return fusenrux(reducer, action.type);
};



