import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import * as playlist from './../../actions/playlist';

class FollowersCount extends Component {
  constructor() {
    super();
  }

  componentDidMount() {
    if (!this.props.app.gridPlaylistList[this.props.playlist] && this.props.auth.loged) {
      this.props.actions.getFollowers(this.props.user, this.props.playlist);
    }
  }

  render() {
    return (
      <span>
        <i className="icon_like_empty"></i>
        {
          this.props.app.gridPlaylistList[this.props.playlist] ? ' '+ this.props.app.gridPlaylistList[this.props.playlist].count : ' ' + 0
        }
      </span>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    app: state.app,
    auth: state.auth
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actions: bindActionCreators(playlist, dispatch)
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FollowersCount);



