import SpotifyClient from './../core/SpotifyClient';
import { emit as aEmit } from './analytics';

let Spotify = SpotifyClient.instance;

const setToken = (token) => {
  return {
    type: 'SET_TOKEN',
    data: token
  };
};

const setUser = (user) => {
  return {
    type: 'SET_USER',
    data: user
  };
};

const setLoginStatus = (loged) => {
  return {
    type: 'SET_LOGIN_STATUS',
    data: loged
  };
};

const setUserTop = (tracks) => {
  return {
    type: 'SET_USER_TOP',
    data: tracks
  };
};

const isLoging = (status) => {
  return {
    type: 'SET_IS_LOGIN',
    data: status
  };
};

const spotifyGetUser = (analytics) => {
  return (dispatch) => {
    Spotify.getUser().then(response => {
      Spotify.getUserTopTracks().then(topTracks => {
        dispatch(setUserTop(topTracks));
        dispatch(setUser(response));
        dispatch(setLoginStatus(true));
        dispatch(isLoging(false));
        if (analytics) {
          aEmit('login', 'end', response.id);
        }
      });
    }).catch(response => {
      dispatch(response);
    });
  };
};

const spotifyLogin = () => {
  return (dispatch) => {
    aEmit('login', 'start', '');
    Spotify.login().then(response => {
      dispatch(setToken(response));
      dispatch(spotifyGetUser(true));
    }).catch(response => {
      dispatch(response);
    });
  };
};

const checkStatus = () => {
  return dispatch => {
    if (localStorage.magic_token_expires && localStorage.magic_token) {
      if (Math.round(new Date().getTime() / 1000) < localStorage.magic_token_expires) {
        Spotify.setToken(localStorage.magic_token);
        dispatch(setToken(localStorage.magic_token));
        dispatch(spotifyGetUser());
      } else {
        dispatch(isLoging(false));
      }
    } else {
      dispatch(isLoging(false));
    }
  };
};

export const login = (ref) => {
  return (dispatch) => {
    aEmit('login', ref, '');
    dispatch(isLoging(true));
    dispatch(spotifyLogin());
  };
};

export const logout = (ref) => {
  return (dispatch) => {
    aEmit('logout', ref, '');
    dispatch(setLoginStatus(false));
    dispatch(setToken(null));
    dispatch(setUser(null));
    localStorage.magic_token_expires = null;
    localStorage.magic_token = null;
  };
};

export const status = () => {
  return (dispatch) => {
    dispatch(isLoging(true));
    dispatch(checkStatus());
  };
};



