import React, { Component } from 'react';
import ReactTooltip from 'react-tooltip';
import Share from './../Share';
import { emit as aEmit } from './../../actions/analytics';

let bodyElement = document.querySelector('body');

const ModalStatusPlaylist = ({ close, features, username, cover, playlist }) => {
  let coverBack = {
    backgroundImage: `url(${cover})`
  };
  // Hide body scroll
  const classHidden = 'modal_hidden';
  if (!bodyElement.classList.contains(classHidden)) {
    bodyElement.classList.add(classHidden);
  }
  return (
    <div className="component_modal_playlist">
      <div className="confirm_container">
        <span className="icon_close" onClick={ close }></span>
        <h4>Well done { username }! Your playlist is waiting for you.</h4>

        <div className="playlist_cover_container">
          <div className="confirm_cover_back"></div>
          <div className="confirm_cover_back"></div>
          <div className="confirm_cover_container" style={ coverBack }>
            <div className="confirm_button_container">
              <a
                className="confirm_button"
                href={ playlist._external_urls.spotify }
                target="_blank"
                onClick={ aEmit.bind(null, 'confirmationModal', 'play', playlist.id) }
              >
                <i className="icon_play"></i>
              </a>
              <div className="confirm_button ripple-out share-slot">
                <span><i className="icon_share"></i></span>
                <Share
                  url={ `http://magicplaylist.co/${playlist.owner.id}/p/${playlist.id}` }
                  image={ cover }
                  title= { playlist.name }
                  place="ModalStatus"
                />
              </div>
            </div>
          </div>
        </div>
        <h5>Your playlist in numbers <span className="icon_help" data-tip="Check out these numbers to find out more about your playlist" data-place="top"></span></h5>
        <div className="confirm_metrics_container">
          <div className="confirm_metrics_item">
            <p>{ features.energy.toFixed(2) }</p>
            <span>Energy</span>
          </div>
          <div className="confirm_metrics_item">
            <p>{ features.loudness.toFixed(2) } <span>dB</span></p>
            <span>Loudness</span>
          </div>
          <div className="confirm_metrics_item">
            <p>{ features.tempo.toFixed(2) } <span>BPM</span></p>
            <span>Tempo</span>
          </div>
          <div className="confirm_metrics_item">
            <p>{ features.valence.toFixed(2) }%</p>
            <span>Valence</span>
          </div>
          <div className="confirm_metrics_item">
            <p>{ features.danceability.toFixed(2) }%</p>
            <span>Danceability</span>
          </div>
        </div>
      </div>
      <div className="modal_overlay" onClick={ close }></div>
    </div>
  );
};

export default ModalStatusPlaylist;



