import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { hashHistory as history } from 'react-router';

import * as suggestionActions from './../../actions/suggestion';
import * as createActions from './../../actions/create';

import SearchBox from './../../components/SearchBox';

class Search extends Component {
  constructor() {
    super();
    this.state = {
      value: ''
    };

    this._getSuggestions = this._getSuggestions.bind(this);
  }

  render() {
    return (
      <SearchBox
        onSearch={this._getSuggestions}
        value={this.state.value}
        suggestions={this.props.suggestion.suggestions}
        onSuggestionSelected={this.props.onSuggestionSelected}
        placeholder={this.props.placeholder}
      />
    );
  }

  _getSuggestions(event, { newValue, method }) {
    if (method !== 'type') return;
    const inputValue = newValue;

    if (inputValue.length > 3) {
      this.props.suggestionActions.getSuggestions(inputValue);
    };

    this.setState({
      value: inputValue
    });
  }
}

const mapStateToProps = (state) => {
  return {
    suggestion: state.suggestion
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    suggestionActions: bindActionCreators(suggestionActions, dispatch),
    createActions: bindActionCreators(createActions, dispatch)
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Search);



