import React, { Component,  } from 'react';
import ModalSave from './../ModalSave';
import Player from './../Player';
import Loading from './../Loading';
import Search from './../../containers/Search';
import Resize from './../resize';
import { emit as aEmit } from './../../actions/analytics';

let bodyElement = document.querySelector('body');

const ModalNewPlaylist = ({ isLoading, tracks, goHome, original, save, removeTrack, isLoged, login, addTrack }) => {
  let audios = [];
  let pushAudio = (track) => {
    audios.push(track);
  };

  let stopAll = () => {
    audios.map(track => {
      track.pause();
    });
  };

  let onSuggestionSelected = (event, { suggestion }) => {
    addTrack(suggestion.id);
    aEmit('createPlaylist', 'addSong', suggestion.id);
  };

  login = login.bind(null, 'savePlaylist');

  // Hide body scroll
  const classHidden = 'modal_hidden';
  if (!bodyElement.classList.contains(classHidden)) {
    bodyElement.classList.add(classHidden);
  }

  let playlist = () => {
    let coverBack = {
      backgroundImage: `url(${original.album.images[0].url})`
    };
    return (
      <div>
        <div className="viewer_playlist">
          <div className="viewer_cover" style={ coverBack }>
            <span className="icon_close" onClick={goHome}></span>
            <div className="viewer_cover_content">
              <div className="viewer_playlist_name">
                <p>Based on <a href="#">{ original.name }, { original.artists[0].name }</a></p>
              </div>
            </div>
            <div className="viewer_gradient"></div>
          </div>
          <div className="viewer_playlist_list">
            <Search onSuggestionSelected={ onSuggestionSelected } placeholder="Add a new song to the playlist" />
            <Resize rest={ 340 }>
              <ul>
                {
                  tracks.map((track, index) => {
                    return (
                      <li key={track.id}>
                        <div className="playlist_song_name">
                          <span>{ index + 1 }</span> { track.name }, { track.artists[0].name }
                        </div>
                        <div className="playlist_song_tools">
                          <Player source={ track.preview_url } pushAudio={ pushAudio } stopAll={ stopAll }/>
                          <a
                            href={ `/#/create/${track.id}` }
                            onClick={ () => {aEmit('createPlaylist', 'remake', track.id);} }
                          >
                            <i className="icon_refresh"></i>
                          </a>
                          <i
                            className="icon_remove"
                            onClick={ () => {removeTrack(index); aEmit('createPlaylist', 'remove', track.id); }}
                          >
                          </i>
                        </div>
                      </li>
                    );
                  })
                }
              </ul>
            </Resize>
          </div>
        </div>
        { <ModalSave goHome={ goHome } save={ save } isLoged={ isLoged } login={ login }/> }
      </div>
      );
  };
  return (
    <div className="component_modal_playlist">
      {
        !isLoading && tracks.length && original.id
        ?
        <div className="viewer_wrapper">
          { playlist() }
        </div>
        :
        <Loading />
      }
      <div className="modal_overlay" onClick={ goHome }></div>
    </div>
  );
};

export default ModalNewPlaylist;



