import React, { Component } from 'react';

let bodyElement = document.querySelector('body');

const ModalError = ({ close, message }) => {
  // Hide body scroll
  const classHidden = 'modal_hidden';
  if (!bodyElement.classList.contains(classHidden)) {
    bodyElement.classList.add(classHidden);
  }
  return (
    <div className="component_modal_playlist">
      <div className="error_container">
        <span className="icon_close" onClick={ close }></span>
        <h4>Oops! We did it again.</h4>
        <p>There was an unexpected problem while serving the requested action.</p>
        <p>Here is what you can try:</p>
        <p><a href="javascript:history.go(0)">Refresh the page</a> (this usually works on temporary problems).</p>
        <p>If the problem persists, <a href="mailto:hello@magicplaylist.co" target="_blank">contact us</a> and we will work to fix it.</p>
      </div>
      <div className="modal_overlay" onClick={ close }></div>
    </div>
  );
};

export default ModalError;



