import { stateTree } from './../store/state';
import fusenrux from './../helpers/fusenrux';

const initialState = Object.assign({}, stateTree.playlist);

export default (state = initialState, action) => {
  const reducer = {};

  reducer.SET_PLAYLIST = () => {
    let newState = Object.assign({}, state);
    newState.playlist = action.data;
    newState.isFollowing = null;
    return newState;
  };

  reducer.IS_PLAYLIST_LOADING = () => {
    let newState = Object.assign({}, state);
    newState.isLoading = action.data;
    newState.isFollowing = null;
    return newState;
  };

  reducer.SET_PLAYLIST_IS_FOLLOWING = () => {
    let newState = Object.assign({}, state);
    newState.isFollowing = action.data;
    return newState;
  };

  reducer.DEFAULT = () => state;

  return fusenrux(reducer, action.type);
};



