import React, { Component } from 'react';
import { emit as aEmit } from './../../actions/analytics';

class Player extends Component {
  constructor() {
    super();
    this.state = {
      loading: false,
      playling: false
    };

    this._setLoading = this._setLoading.bind(this);
    this._setPlaying = this._setPlaying.bind(this);
    this._play = this._play.bind(this);
    this._stop = this._stop.bind(this);
    this._listeners = {
      setPlayingFalse: () => {
        this._setPlaying(false);
      },
      setLoadingFalse: () => {
        this._setLoading(false);
      },
      setLoadingTrue: () => {
        this._setLoading(true);
      }
    };
  }

  componentDidMount() {
    this._audioElement = this.refs.audio;
    this.props.pushAudio(this._audioElement);
    this._audioElement.addEventListener('ended', this._listeners.setPlayingFalse);
    this._audioElement.addEventListener('pause', this._listeners.setPlayingFalse);
    this._audioElement.addEventListener('loadeddata', this._listeners.setLoadingFalse);
    this._audioElement.addEventListener('loadstart', this._listeners.setLoadingTrue);
    this._audioElement.addEventListener('suspend', this._listeners.setLoadingFalse);
  }

  componentWillUnmount() {
    this._audioElement.removeEventListener('ended', this._listeners.setPlayingFalse);
    this._audioElement.removeEventListener('pause', this._listeners.setPlayingFalse);
    this._audioElement.removeEventListener('loadeddata', this._listeners.setLoadingFalse);
    this._audioElement.removeEventListener('loadstart', this._listeners.setLoadingTrue);
    this._audioElement.removeEventListener('suspend', this._listeners.setLoadingFalse);
  }

  render() {
    let classNamePlayer = this.state.playling ? '' : 'paused';
    return (
      <div className="player-component">
        <div
          className={`play-button ${classNamePlayer}`}
          onClick={ this.state.playling ? this._stop : this._play }
        >
          <div className="left"></div>
          <div className="right"></div>
          <div className="triangle-1"></div>
          <div className="triangle-2"></div>
        </div>
        <audio ref="audio" src={ this.props.source } preload="none"/>
      </div>
      );
  }

  _play() {
    this.props.stopAll();
    this._audioElement.play();
    this.setState({
      playling: true
    });
    aEmit('previewPlayer', 'play');
  }

  _stop() {
    this._audioElement.pause();
    this.setState({
      playling: false
    });
    aEmit('previewPlayer', 'stop');
  }

  _setLoading(value) {
    this.setState({
      loading: value
    });
  }

  _setPlaying(value) {
    this.setState({
      playling: value
    });
  }
}

export default Player;



