import React, { Component } from 'react';
import ModalDetails from './../ModalDetails';
import ModalSave from './../ModalSave';
import Player from './../Player';
import ModalLogin from './../ModalLogin';
import Loading from './../Loading';
import Resize from './../resize';
import { emit as aEmit } from './../../actions/analytics';

import PLAYLISTS from './../../core/playlists';

let bodyElement = document.querySelector('body');

let audios = [];
let pushAudio = (track) => {
  audios.push(track);
};
let stopAll = () => {
  audios.map(track => {
    track.pause();
  });
};

let backgroundMiddelware = (playlistId, url) => {
  return PLAYLISTS[playlistId] ? PLAYLISTS[playlistId].image : url;
};

const ModalViewPlaylist = ({ isLoading, playlist, goHome, follow, showCreate, showingCreate, save, isLoged, login, isLogin }) => {
  login = login.bind(null, 'viewPlaylist');

  // Hide body scroll
  const classHidden = 'modal_hidden';
  if (!bodyElement.classList.contains(classHidden)) {
    bodyElement.classList.add(classHidden);
  }

  let playlistC = () => {
    let coverBack = {
      backgroundImage: `url(${backgroundMiddelware(playlist.id, playlist.images[0].url)})`
    };
    return (
      <div>
        <div className="viewer_playlist">
          <div className="viewer_cover" style={ coverBack }>
            <span className="icon_close" onClick={goHome}></span>
            <div className="viewer_cover_content">
              <div className="viewer_playlist_name">
                <h3>{ playlist.name }</h3>
              </div>
            </div>
            <div className="viewer_gradient"></div>
          </div>
          <div className="viewer_playlist_list">
            <Resize rest={ 300 }>
              <ul>
                {
                  playlist.tracks.map((track, index) => {
                    return (
                      <li key={track.id}>
                        <div className="playlist_song_name">
                          <span>{ index + 1 }</span> { track.name }, { track.artists[0].name }
                        </div>
                        <div className="playlist_song_tools">
                          <Player source={ track.preview_url } pushAudio={ pushAudio } stopAll={ stopAll }/>
                          <a
                            href={ `/#/create/${track.id}` }
                            onClick={ () => {aEmit('createPlaylist', 'remake', track.id);} }
                          >
                            <i className="icon_refresh"></i>
                          </a>
                        </div>
                      </li>
                    )
                  })
                }
              </ul>
            </Resize>
          </div>
        </div>
        {
          showingCreate
          ?
          <ModalSave goHome={ showCreate } save={ save } isLoged={ isLoged } login={ login }/>
          :
          <ModalDetails playlist={ playlist } follow={ follow } showCreate= { showCreate } />
        }
      </div>
      );
  };
  return (
    <div className="component_modal_playlist">
        {
          !isLoading && playlist
          ?
          <div className="viewer_wrapper">
            { playlistC() }
          </div>
          :
          isLoged || isLogin
          ?
          <Loading />
          :
          <ModalLogin login={ login }/>
        }
      <div className="modal_overlay" onClick={goHome}></div>
    </div>
  );
};

export default ModalViewPlaylist;



