import Singleton from './Singleton';
import { Client, UserHandler, TrackHandler, PlaylistHandler } from 'spotify-sdk';
import magic from './Magic';

class SpotifyClient extends Singleton {
  constructor() {
    super();
    this.spotiClient = Client.instance;
    //live
    this.spotiClient.settings = {
      clientId: '898cb61111804d2c8bf225394482e209',
      secretId: 'b1efa6e07d83404fae8f7e5178349d64',
      scopes: 'playlist-modify-public playlist-modify-private user-top-read',
      redirect_uri: 'http://magicplaylist.co/login/index.html'
    };

    // dev
    this.spotiClient.settings = {
      clientId: '87e58d70ae454ae7b815b4c8a1556a98',
      secretId: '52bf6c6b92a04e489e8899fc0d291d5f',
      scopes: 'playlist-modify-public playlist-modify-private user-top-read',
      redirect_uri: 'http://localhost:8080/login/index.html'
    };

    this.user = new UserHandler();
    this.track = new TrackHandler();
    this.playlist = new PlaylistHandler();
  }

  login() {
    return new Promise((resolve) => {
      this.spotiClient.login((url) => {
        window.open(
          url,
          'Spotify',
          'menubar=no,location=no,resizable=yes,scrollbars=yes,status=no,width=400,height=500'
        );
        window.addEventListener('storage', (data) => {
          if (data.key === 'magic_token') {
            // Set current token
            this.spotiClient.token = data.newValue;
            resolve(data.newValue);
          }
        });
      });
    });
  }

  setToken(token) {
    this.spotiClient.token = token;
  }

  getUser() {
    return this.user.me();
  }

  getUserTopArtists() {
    return this.user.top('artists', { limit: 10 });
  }

  getUserTopTracks() {
    return this.user.top('tracks', { limit: 50 }).then(response => {
      let trackList = {};
      response.map(track => {
        if (!trackList[track.artists[0].id]) {
          trackList[track.artists[0].id] = track;
        }
      });
      let ids = Object.keys(trackList).map(id => {
        return trackList[id].id;
      });

      let length = 8;
      if (ids.length < 8 && ids.length >= 4) {
        length = 4;
      } else if (ids.length < 4) {
        length = ids.length;
      }
      ids.length = length;
      return this.track.get(ids);
    });
  }

  search(text) {
    return this.track.search(text, { limit: 5, market: 'AR' });
  }

  getPlaylist(userId, playlistId) {
    return this.user.playlists(userId, playlistId)
      .then(playlistEntity => {
        return this.user.get(playlistEntity.owner.id).then(userEntity => {
          playlistEntity.owner = userEntity;
          return playlistEntity;
        });
      });
  }

  createPlaylist(trackId, country) {
    return new Promise((resolve, fail) => {
      let total;
      let trackList = [];

      this.track.get(trackId).then(track => {
        track.artists.first().relatedArtists().then((relatedArtists) => {
          relatedArtists = relatedArtists.slice(0, 10);
          if (relatedArtists.length) {
            relatedArtists.push(track.artists.first());
            for (var i = relatedArtists.length - 1; i >= 0; i--) {
              total = relatedArtists.length - 1;
              relatedArtists[i].topTracks({country: country}).then((tracks) => {
                if (tracks.length) {
                  for (var e = tracks.length - 1; e >= 0; e--) {
                    trackList.push(tracks[e]);
                    if (e === 0) {
                      total -= 1;
                      if (total === 0) {
                        resolve({
                          playlist: magic.magic(trackList, track.popularity, track),
                          original: track
                        });
                      }
                    }
                  };
                } else {
                  total -= 1;
                }
              }).catch(fail);
            };
          } else {
            callback([]);
          }
        }).catch(fail);
      }).catch(fail)
    });
  }

  savePlaylist(userId, name, isPublic, tracks) {
    return new Promise((resolve, reject) => {
      this.playlist.create(userId, name + ' by magicplaylist.co', isPublic).then((myPlaylist) => {
        myPlaylist.addTrack(tracks).then(snapshot => {
          resolve(myPlaylist);
        });
      }).catch(error => {
        reject(error);
      });
    });
  }

  followPlaylist(userId, playlistId) {
    return this.playlist.follow(userId, playlistId);
  }

  unfollowPlaylist(userId, playlistId) {
    return this.playlist.unfollow(userId, playlistId);
  }

  isFollowing(ownerId, playlistId, userId) {
    return this.playlist.contains(playlistId, ownerId, userId);
  }

  getTrack(id) {
    return this.track.get(id);
  }

  getTracksFeatureResume(ids) {
    return this.track.audioFeatures(ids).then(tracks => {
      let total = tracks.audio_features.length;
      let result = {
        energy: 0,
        loudness: 0,
        tempo: 0,
        valence: 0,
        danceability: 0
      };

      tracks.audio_features.map(feature => {
        result.energy = result.energy + feature.energy;
        result.loudness = result.loudness + feature.loudness;
        result.tempo = result.tempo + feature.tempo;
        result.valence = result.valence + feature.valence;
        result.danceability = result.danceability + feature.danceability;
      });

      return {
        energy: result.energy / total,
        loudness: result.loudness / total,
        tempo: result.tempo / total,
        valence: result.valence / total,
        danceability: result.danceability / total
      };
    });
  }

}

export default SpotifyClient;



