import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { hashHistory as history } from 'react-router';

import * as authActions from './../../actions/auth';
import * as appActions from './../../actions/app';
import * as createActions from './../../actions/create';
import * as playlistActions from './../../actions/playlist';
import { emit as aEmit } from './../../actions/analytics';

import HeroContainer from './../Hero';
import ModalViewPlaylist from './../../components/ModalViewPlaylist';
import ModalNewPlaylist from './../../components/ModalNewPlaylist';
import GridPlaylists from './../../components/GridPlaylists';
import ModalStatusPlaylist from './../../components/ModalStatusPlaylist';
import Footer from './../../components/Footer';
import ModalError from './../../components/ModalError';
import Feedback from './../../components/Feedback';

import PLAYLISTS from './../../core/playlists';

let MPplaylists = [];

class HomeContainer extends Component {
  constructor(props) {
    super();
    props.authActions.status();
    props.appActions.getCountry();
    this.state = {
      showCreate: false
    };

    this.bodyElement = document.querySelector('body');

    this._savePlaylist = this._savePlaylist.bind(this);
    this._followPlaylist = this._followPlaylist.bind(this);
    this._showCreate = this._showCreate.bind(this);
    this._savePlaylistView = this._savePlaylistView.bind(this);
    this._goHome = this._goHome.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.params.trackId !== this.props.params.trackId && nextProps.params.trackId) {
      this.props.createActions.createPlaylsit(nextProps.params.trackId, this.props.app.country);
    }

    if (nextProps.params.trackId === this.props.params.trackId && nextProps.params.trackId && nextProps.app.country !== this.props.app.country) {
      this.props.createActions.createPlaylsit(nextProps.params.trackId, nextProps.app.country);
    }

    if (nextProps.params.playlistId !== this.props.params.playlistId && nextProps.params.playlistId && this.props.auth.loged) {
      this.props.playlistActions.getPlaylist(nextProps.params.userId, nextProps.params.playlistId);
    }

    if (this.props.auth.loged !== nextProps.auth.loged && this.props.params.playlistId) {
      this.props.playlistActions.getPlaylist(nextProps.params.userId, nextProps.params.playlistId);
    }
  }

  render() {
    let create = this.props.create;
    let playlist = this.props.playlist;
    return (
      <div>
        <HeroContainer/>
        {
          this.props.auth.loged && this.props.auth.topTracks.length >= 1
          ?
          <GridPlaylists
            playlists={ this.props.auth.topTracks }
            title={ `${this.props.auth.user._display_name}, discover your next playlist with these songs` }
            />
          :
          ''
        }
        <GridPlaylists
          playlists={ this._getRandomPlaylsits() }
          title="Recent playlists"
          hard={ true }
          loged={ this.props.auth.loged }
        />
        {
          this.props.params.playlistId
          ?
          <ModalViewPlaylist
            isLoading={playlist.isLoading}
            isLogin={ this.props.auth.isLogin }
            playlist={playlist.playlist}
            goHome={this._goHome}
            follow={ this._followPlaylist }
            showCreate={ this._showCreate }
            showingCreate={ this.state.showCreate }
            save={ this._savePlaylistView }
            isLoged={ this.props.auth.loged }
            login={ this.props.authActions.login }
          />
          :
          this._removeHidden()
        }
        {
          this.props.params.trackId
          ?
          <ModalNewPlaylist
            isLoged={ this.props.auth.loged }
            login={ this.props.authActions.login }
            isLoading={create.isLoading}
            tracks={create.tracks}
            original={create.track}
            goHome={this._goHome}
            save={ this._savePlaylist }
            removeTrack={this.props.createActions.removeTrack}
            addTrack={this.props.createActions.addTrack}
          />
          :
          this._removeHidden()
        }
        {
          this.props.create.modalConfirm
          ?
          <ModalStatusPlaylist
            close={ this.props.createActions.closeConfirmModal }
            features={ create.tracksFeatures }
            username={ this.props.auth.user._display_name }
            cover={ create.track.album.images[0].url }
            playlist={ create.savedPlaylist }
          />
          :
          this._removeHidden()
        }
        {
          this.props.app.modalError.show
          ?
          <ModalError
            message={ this.props.app.modalError.message }
            close={ this.props.appActions.hideModalError }
          />
          :
          this._removeHidden()
        }
        <Feedback />
        <Footer login={ this.props.authActions.login } loged={ this.props.auth.loged }/>
      </div>
    );
  }

  _getRandomPlaylsits() {
    let shuffle = (arr) => {
      let i, j,temp;
      for (i = arr.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        temp = arr[i];
        arr[i] = arr[j];
        arr[j] = temp;
      }
      return arr;
    };
    if (MPplaylists.length === 0) {
      MPplaylists = shuffle(Object.keys(PLAYLISTS).map(id => PLAYLISTS[id]));
      MPplaylists.length = 10;
    }
    return MPplaylists;
  }

  _goHome() {
    history.push('/');
    this.setState({
      showCreate: false
    });
  }

  _savePlaylist(settings) {
    if (settings.name !== null && settings.privacy !== null) {
      this.props.createActions.savePlaylist(this.props.auth.user.id, settings.name, settings.privacy, this.props.create.tracks, this.props.create.track.id);
      aEmit('magicData', 'idTrack', this.props.create.track.id);
      aEmit('magicData', 'idArtist', this.props.create.track.artists[0].id);
    }
  }

  _savePlaylistView(settings) {
    if (settings.name !== null && settings.privacy !== null) {
      this.props.createActions.savePlaylist(this.props.auth.user.id, settings.name, settings.privacy, this.props.playlist.playlist.tracks, this.props.create.track.id);
      this.setState({
        showCreate: !this.state.showCreate
      });
      aEmit('magicData', 'idTrack', this.props.create.track.id);
      aEmit('magicData', 'idArtist',this.props.create.track.artists[0].id);
    }
  }

  _followPlaylist() {
    this.props.playlistActions.follow(this.props.playlist.playlist.owner.id, this.props.playlist.playlist.id);
  }

  _showCreate() {
    this.setState({
      showCreate: !this.state.showCreate
    });
  }

  _removeHidden() {
    const classHidden = 'modal_hidden';
    if (this.bodyElement.classList.contains(classHidden)) {
      this.bodyElement.classList.remove(classHidden);
    }
    return '';
  }
}

const mapStateToProps = (state) => {
  return {
    auth: state.auth,
    create: state.create,
    app: state.app,
    playlist: state.playlist,
    gridPlaylist: state.gridPlaylist
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    authActions: bindActionCreators(authActions, dispatch),
    appActions: bindActionCreators(appActions, dispatch),
    createActions: bindActionCreators(createActions, dispatch),
    playlistActions: bindActionCreators(playlistActions, dispatch)
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomeContainer);



