import React, { Component,  } from 'react';
import { hashHistory as history } from 'react-router';
import { emit as aEmit } from './../../actions/analytics';

let bodyElement = document.querySelector('body');

const ModalLogin = ({ login }) => {
  login = login.bind(null, 'modal');
  let cancelLogin = () => {
    history.push('/');
    aEmit('login', 'modal', 'cancel');
  };
  // Hide body scroll
  const classHidden = 'modal_hidden';
  if (!bodyElement.classList.contains(classHidden)) {
    bodyElement.classList.add(classHidden);
  }
  return (
    <div className="login_container">
      <h4>Please sign in to start.</h4>
      <p>Yes, we know that awkward feeling. You have to sign in with your Spotify account in order to watch this content. But don’t worry! We have great stuff waiting for you on the otherside.</p>
      <div className="signin_btn" onClick={ login }><i className="icon_spotify"></i> Connect with Spotify</div>
      <span onClick={ cancelLogin }>Maybe next time. Thanks!</span>
    </div>
  );
};

export default ModalLogin;



