import React, { Component } from 'react';
import { emit as aEmit } from './../../actions/analytics';

const Feedback = () => {
  return (
    <a
      className="feedback_btn"
      onClick={ () => { aEmit('Feedback', 'press') } }
      href="http://goo.gl/forms/oFWy0XCBPla8lV8z1"
      target="_blank"
      data-place="left"
      data-tip="Give us your feedback"
    >
    	<i className="icon_feedback"></i>
    </a>
  );
};

export default Feedback;



