import { hashHistory as history } from 'react-router';
import { emit as aEmit } from './../actions/analytics';

const setCountry = (country) => {
  return {
    type: 'SET_COUNTRY',
    data: country
  };
};

export const getCountry = () => {
  return (dispatch) => {
    if (localStorage.magic_country) {
      dispatch(setCountry(localStorage.magic_country));
    } else {
      let checkStatus = (response) => {
        if (response.status >= 200 && response.status < 300) {
          return response;
        } else {
          let error = new Error(response.statusText);
          error.response = response;
          throw error;
        }
      };

      let parseJSON = (response) => {
        return response.json();
      };

      fetch('http://ip-api.com/json', {
        method: 'GET'
      })
      .then(checkStatus)
      .then(parseJSON)
      .then((response) => {

        let markets = ['AD','AR','AT','AU','BE','BG','BO','BR','CA','CH','CL','CO','CR','CY','CZ',
        'DE','DK','DO','EC','EE','ES','FI','FR','GB','GR','GT','HK','HN','HU','IE','IS','IT','LI',
        'LT','LU','LV','MC','MT','MX','MY','NI','NL','NO','NZ','PA','PE','PH','PL','PT','PY','RO',
        'SE','SG','SI','SK','SV','TR','TW','US','UY'];

        if (markets.indexOf(response.countryCode) > -1) {
          localStorage.magic_country = response.countryCode;
          dispatch(setCountry(response.countryCode));
        }
      })
      .catch(() => {
        dispatch(setCountry('US'));
      });
    }
  };
};

export const modalError = (show, message = '') => {
  return {
    type: 'MODAL_ERROR',
    data: {
      show: show,
      message: message
    }
  };
};

export const showModalError = (message) => {
  aEmit('modalError', 'show');
  return modalError(true, message);
};

export const hideModalError = () => {
  history.push('/');
  aEmit('modalError', 'hide');
  return modalError(false);
};



