import React, { Component } from 'react';

const User = ({ login, logout, user, isLoged }) => {
  // Analytics parameters
  login = login.bind(null, 'home');
  logout = logout.bind(null, 'home');
  return (
    <ul>
    {
      isLoged
      ?
      <li className="user">
        <span>{ user._display_name } <a href="#" onClick={ logout }>(logout)</a></span> <img src={ user.images[0].url } alt="user avatar"/>
      </li>
      :
      <li><a className="signin_btn" href="#" onClick={ login }><i className="icon_spotify"></i> Connect with Spotify</a></li>
    }
    </ul>
  );
};

export default User;



