import React, { Component } from 'react';
import ReactDisqusThread from 'react-disqus-thread';
import ReactTooltip from 'react-tooltip';

import Share from './../Share';
import FollowPlaylist from './../../containers/FollowPlaylist'
import { emit as aEmit } from './../../actions/analytics';

const ModalDetail = ({ playlist, follow, showCreate }) => {
  return (
    <div className="viewer_author">
      <div className="viewer_playlist_tools">
        <a className="viewer_playlist_tools_item" data-tip="Play in Spotify" data-place="bottom"
          href={ playlist.external_urls.spotify }
          target="_blank"
          onClick={ () => {aEmit('viewPlaylist', 'playPlaylist', playlist.id); }}
        >
            <span className="icon_play"></span>
        </a>
        <div className="viewer_playlist_tools_item" onClick={ () => {showCreate(); aEmit('viewPlaylist', 'save', playlist.id);} } data-tip="Save playlist" data-place="bottom">
          <span><i className="icon_arrow_down"></i></span>
        </div>

        <FollowPlaylist playlist={ playlist }/>

        <div className="share-slot viewer_playlist_tools_item">
          <span><i className="icon_share"></i></span>
          <Share
            url={ `http://magicplaylist.co/${playlist.owner.id}/p/${playlist.id}` }
            image={ playlist.images[0].url }
            title={ playlist.name }
            place="ModalDetail"
          />
        </div>
      </div>
      <div className="viewer_author_profile" >
        <div className="profile_avatar" style={ { backgroundImage: `url(${playlist.owner.images[0].url})`} }>
        </div>
        <p>{ playlist.owner._display_name }</p>
      </div>
      <div className="viewer_comments">
        <ReactDisqusThread
          shortname="magicplaylist"
          identifier={ `${playlist.id}` }
          url={ `http://localhost:8000/${playlist.owner.id}/p/${playlist.id}` }
        />
      </div>
    <ReactTooltip effect="solid"/>
    </div>
  );
};

export default ModalDetail;



