import { stateTree } from './../store/state';
import fusenrux from './../helpers/fusenrux';

const initialState = Object.assign({}, stateTree.auth);

export default (state = initialState, action) => {
  const reducer = {};

  reducer.SET_TOKEN = () => {
    let newState = Object.assign({}, state);
    newState.token = action.data;
    return newState;
  };

  reducer.SET_USER = () => {
    let newState = Object.assign({}, state);
    newState.user = action.data;
    return newState;
  };

  reducer.SET_USER_TOP = () => {
    let newState = Object.assign({}, state);
    newState.topTracks = action.data;
    return newState;
  };

  reducer.SET_LOGIN_STATUS = () => {
    let newState = Object.assign({}, state);
    newState.loged = action.data;
    return newState;
  };

  reducer.SET_IS_LOGIN = () => {
    let newState = Object.assign({}, state);
    newState.isLogin = action.data;
    return newState;
  };

  reducer.DEFAULT = () => state;

  return fusenrux(reducer, action.type);
};



