import { stateTree } from './../store/state';
import fusenrux from './../helpers/fusenrux';

const initialState = Object.assign({}, stateTree.create);

export default (state = initialState, action) => {
  const reducer = {};

  reducer.SET_NEW_PLAYLIST = () => {
    let newState = Object.assign({}, state);
    newState.tracks = action.data.playlist;
    newState.track = action.data.original;
    newState.isLoading = false;
    return newState;
  };

  reducer.SET_TRACK = () => {
    let newState = Object.assign({}, state);
    newState.track = action.data;
    return newState;
  };

  reducer.IS_NEW_PLAYLIST_LOADING = () => {
    let newState = Object.assign({}, state);
    newState.isLoading = action.data;
    return newState;
  };

  reducer.REMOVE_TRACK = () => {
    let newState = Object.assign({}, state);
    newState.tracks.splice(action.data, 1);
    return newState;
  };

  reducer.PUSH_TRACK = () => {
    let newState = Object.assign({}, state);
    newState.tracks.unshift(action.data);
    return newState;
  };

  reducer.CONFIRM_MODAL_STATUS = () => {
    let newState = Object.assign({}, state);
    newState.modalConfirm = action.data;
    return newState;
  };

  reducer.SET_TRACKS_FEATURES = () => {
    let newState = Object.assign({}, state);
    newState.tracksFeatures = action.data;
    return newState;
  };

  reducer.SET_SAVED_PLAYLIST = () => {
    let newState = Object.assign({}, state);
    newState.savedPlaylist = action.data;
    return newState;
  };

  reducer.DEFAULT = () => state;

  return fusenrux(reducer, action.type);
};



